@import "https://fonts.googleapis.com/css2?family=Montserrat&display=swap";
body {
  max-width: 1200px;
  margin: auto;
  padding: 16px;
  font-family: Montserrat, sans-serif;
}

input[type="range"] {
  accent-color: #e00;
}

header {
  text-align: center;
  color: #fff;
  background-color: #e00;
  flex-direction: row;
  align-items: center;
  margin-bottom: 16px;
  padding: 8px;
  display: flex;
}

header img {
  height: 60px;
  margin-left: 1rem;
}

header h1 {
  flex-grow: 1;
}

main {
  flex-direction: row;
  display: flex;
}

.filtres {
  border-radius: 4px;
  min-width: 300px;
  min-height: 400px;
  margin: 8px;
  padding: 8px;
  box-shadow: 0 0 4px gray;
}

.filtres h3 {
  text-align: center;
}

.fiches {
  flex: 1;
  grid-template-columns: repeat(3, 1fr);
  gap: 4px;
  margin: 8px;
  display: grid;
}

.fiches article {
  border: 1px solid #000;
  padding: 4px;
}

.fiches article img {
  max-width: 200px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.fiches article p {
  margin-top: 4px;
  margin-bottom: 4px;
}

.fiches article h2 {
  text-align: center;
}

